import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { StorePage } from '../pages/store/store';
import { HotPage } from '../pages/hot/hot';
import { TabsControllerPage } from '../pages/tabs-controller/tabs-controller';
import { Uncoverb29Page } from '../pages/uncoverb29/uncoverb29';
import { Uncover226Page } from '../pages/uncover226/uncover226';
import { Electra1141Page } from '../pages/electra1141/electra1141';
import { Doubleh3lixPage } from '../pages/doubleh3lix/doubleh3lix';
import { YoutubePage } from '../pages/youtube/youtube';


import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    StorePage,
    HotPage,
    TabsControllerPage,
    Uncoverb29Page,
    Uncover226Page,
    Electra1141Page,
    Doubleh3lixPage,
    YoutubePage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    StorePage,
    HotPage,
    TabsControllerPage,
    Uncoverb29Page,
    Uncover226Page,
    Electra1141Page,
    Doubleh3lixPage,
    YoutubePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}


