import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Uncoverb29Page } from '../uncoverb29/uncoverb29';
import { Uncover226Page } from '../uncover226/uncover226';
import { Electra1141Page } from '../electra1141/electra1141';
import { Doubleh3lixPage } from '../doubleh3lix/doubleh3lix';
import { YoutubePage } from '../youtube/youtube';

@Component({
  selector: 'page-store',
  templateUrl: 'store.html'
})
export class StorePage {
  // this tells the tabs component which Pages
  // should be each tab's root Page
  constructor(public navCtrl: NavController) {
  }
  goToUncoverb29(params){
    if (!params) params = {};
    this.navCtrl.push(Uncoverb29Page);
  }goToUncover226(params){
    if (!params) params = {};
    this.navCtrl.push(Uncover226Page);
  }goToElectra1141(params){
    if (!params) params = {};
    this.navCtrl.push(Electra1141Page);
  }goToDoubleh3lix(params){
    if (!params) params = {};
    this.navCtrl.push(Doubleh3lixPage);
  }goToYoutube(params){
    if (!params) params = {};
    this.navCtrl.push(YoutubePage);
  }
}



